<template>
  <div class="container">
    <div
      v-if="Object.keys(lists).length === 0"
      class="no-task"
      style="width: 800px;"
    >
      <i class="el-icon-info" />
      还没有完成待办事项呢！
    </div>
    <div v-else style="width: 800px;">
      <el-card
        v-for="(item, name, index) in handle_data"
        :key="index"
        class="box-card"
        shadow="hover"
      >
        <div class="completed-title">{{ name }}</div>
        <div
          class="completed-content"
          v-for="(task, index) in item"
          :key="index"
        >
          {{ task.title }}
        </div>
      </el-card>
    </div>
    <div
      class="more-btn"
      @click="hide = !hide"
      v-if="Object.keys(lists).length > 5"
    >
      <div v-show="hide">
        <i class="el-icon-arrow-down" />
        查看更多
      </div>
      <div v-show="!hide">
        <i class="el-icon-arrow-up" />
        收起
      </div>
    </div>
  </div>
</template>

<script>
import { getCompleted } from "../../api/task";

export default {
  data() {
    return {
      lists: {},
      hide: true
    };
  },
  methods: {
    fetch_data() {
      getCompleted(this.$store.getters.token).then(response => {
        this.lists = response.data;
      });
    }
  },
  computed: {
    handle_data() {
      if (this.hide) {
        let show_item = Object.keys(this.lists).slice(0, 5);
        let show_data = {};
        show_item.forEach(item => {
          show_data[item] = this.lists[item];
        });
        return show_data;
      } else {
        return this.lists;
      }
    }
  },
  created() {
    this.fetch_data();
  }
};
</script>

