  margin-bottom: 30px;
}

.completed-title {
  font-size: large;
  font-weight: bold;
  padding: 10px 0;
  margin-bottom: 30px;
  border: none;
  border-bottom: 1px solid #dcdfe6;
}

.completed-content {
  margin-bottom: 10px;
  text-decoration: line-through;
}

.more-btn {
  display: flex;
  justify-content: center;
